.main-nav {
  width: 90%;
  display: flex;
  margin-top: 30px;
  padding: 0;
  justify-content: flex-end;
  align-items: center;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 146.28%;
  letter-spacing: 0.27em;
  text-transform: uppercase;

  &__burger {
    display: none;
    margin: 0;
    padding: 0;

    @media (max-width: 768px) {
      display: flex;
      position: absolute;
      width: 20px;
      top: 28px;
      right: 18px;
      flex-wrap: wrap;
      background-color: transparent;
      border: none;
      cursor: pointer;
    }

    span.line {
      display: inline-block;
      margin-bottom: 4px;
      background-color: #fff;
      width: 100%;
      height: 3px;
    }
  }

  &__burger:hover {
    background-color: transparent;
  }

  &__burger-menu {
    display: inline-block;
    width: 200px;
    position: absolute;
    top: 60px;
    right: 12px;
    padding: 10px;
    background-color: #fff;
    line-height: 1.5;
    opacity: 0.9;
    z-index: 2;

    @media (min-width: 768px) {
      display: none;
    }
  }

  &__burger-list {
    display: none;

    @media (max-width: 768px) {
      display: flex;
      padding: 0;
      margin: 0;
      flex-direction: column;
      align-items: flex-end;
    }
  }

  &__burger-item {
    margin: 0;
    padding-bottom: 20px;
    padding-right: 22px;
    padding-left: 20px;
    font-size: 20px;
  }

  &__burger-link {
    margin: 0;
    padding: 0;
    text-decoration: none;
    color:#000;
    font-weight: 500;
  }

  &__burger-link:hover {
    margin: 0;
    padding: 0;
    text-decoration: none;
    color: #494949;
    font-weight: 700;
  }
  
  &__list {
    display: none;

    @media (min-width: 768px) {
      display: flex;
      flex-direction: row;
      margin: 0;
      padding: 0;
    }
  }
  
  &__item {
    margin-right: 54px;

    &--active {
    }
  }

  &__link {
    list-style: none;
    color: white;
    cursor: pointer;
    font-size: 15px;
    text-transform: uppercase;
  }
  
  &__icons {
    display: flex;
    margin: 0;
    padding-right: 60px;
    padding-left: 224px;
    flex-direction: row;
    
    &--facebook {
      margin: 0;
      padding: 0;
    }

    &--instagram {
      margin: 0;
      padding-right: 35px;
    }
  }
}

.close:before, .close:after {
  position: absolute;
  content: ' ';
  right: 8px;
  top: 0;
  height: 24px;
  width: 3px;
  background-color: #fff;
  cursor: pointer;
}

.close:before {
  transform: rotate(45deg);
}

.close:after {
  transform: rotate(-45deg);
}